import React from 'react';
import './App.css';
import { getWeb3, getInstance}  from "./Web3Util";
import { Link} from "react-router-dom";
import RuneImageComponent from "./components/RuneImageComponent"
import getCurrencyTokenName from "./components/currencyUtils"

const TOTAL_SUPPLY = 511
const RUNE_PRICES = [0,0.75,1.5,3,6,12]

class MarketPlaceHome extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      user: '',
      contractInstance: '',
      user:'',
      networkId:"",
      runesRemainingToAssign:-1,
      myRuneCounter:0,
      myRunes:[]
    };
    this.LoadData = this.LoadData.bind(this);
    this.claimFreeNFT = this.claimFreeNFT.bind(this);
    this.claimPayedNFT = this.claimPayedNFT.bind(this);
    this.getClaimRunesComponent = this.getClaimRunesComponent.bind(this);
  }
  componentWillUnmount(){
    window.scrollTo(0, 0);
  }
  componentDidMount = async () => {
    const web3 = await getWeb3();
    window.web3 = web3;
    const contractInstance = await getInstance(web3);
    window.user = (await web3.eth.getAccounts())[0];
    const networkId = await web3.eth.net.getId();
    this.setState({ user: window.user,hasData:true,contractInstance,networkId });
    
    await this.LoadData(web3);

  }
  async LoadData() {
      try {

       const runesRemainingToAssign = await this.state.contractInstance.methods.runesRemainingToAssign().call();
       const myRuneCounter = await this.state.contractInstance.methods.balanceOf(this.state.user).call();
       const myRunes = await this.state.contractInstance.methods.getAllRunesAtAddress(this.state.user).call();

        this.setState({
          runesRemainingToAssign,
          myRuneCounter,
          myRunes
        })
    } catch (e) {console.log('Error', e)}
  
  }
  async claimFreeNFT() {
    try {
      await this.state.contractInstance.methods.claimFreeRunes().send()
      await this.LoadData(window.web3);
    } catch (e) {console.log('Error', e)}
  };

  async claimPayedNFT(cost) {
    try {
     const priceInWei =  window.web3.utils.toWei(cost, 'ether');
     await this.state.contractInstance.methods.claimPayedRunes().send(
      { value:priceInWei}
     )
      await this.LoadData(window.web3);
    } catch (e) {console.log('Error', e)}
  };

  getClaimRunesComponent(){
    let component = []

    let networkId = this.state.networkId
    let currency = getCurrencyTokenName(networkId)


    if(this.state.runesRemainingToAssign<=TOTAL_SUPPLY){
      if(this.state.runesRemainingToAssign>411){
        component.push(
          <a href="#" onClick={this.claimFreeNFT}>
            Claim your rune for 0.00 {currency}
          </a>
        )
      }else if(this.state.runesRemainingToAssign<=411 &&this.state.runesRemainingToAssign>= 311){
        component.push(
          <a href="#"  onClick={()=>{this.claimPayedNFT("0.75")}}>
            Claim your rune for {RUNE_PRICES[1]} {currency}
          </a>
        )
      }else if(this.state.runesRemainingToAssign<311 &&this.state.runesRemainingToAssign>= 211){
        component.push(
          <a href="#"  onClick={()=>{this.claimPayedNFT("1.5")}}>
            Claim your rune for {RUNE_PRICES[2]} {currency}
          </a>
        )
      }else if(this.state.runesRemainingToAssign<211 &&this.state.runesRemainingToAssign>= 111){
        component.push(
          <a href="#" onClick={()=>{this.claimPayedNFT("3")}}>
            Claim your rune for {RUNE_PRICES[3]} {currency}
          </a>
        )
      }else if(this.state.runesRemainingToAssign<111 &&this.state.runesRemainingToAssign>= 11){
        component.push(
          <a href="#" onClick={()=>{this.claimPayedNFT("6")}}>
            Claim your rune for {RUNE_PRICES[4]} {currency}
          </a>
        )
      }else if(this.state.runesRemainingToAssign<11 &&this.state.runesRemainingToAssign> 0){
        component.push(
          <a href="#"  onClick={()=>{this.claimPayedNFT("12")}}>
            Claim your rune for {RUNE_PRICES[5]} {currency}
          </a>
        )
      }


    }

    return component;
  }

  render() {
    let tempId = 510-this.state.runesRemainingToAssign
    if (this.state.hasData) {
      let currencyLocal = getCurrencyTokenName(this.state.networkId)
      return (
        <div className="App"   >
          <section >
            <div >
              <div>
           
              <br/>
      <h1>Runes to be claimed: {this.state.runesRemainingToAssign == -1?<>loading...</>:<>{this.state.runesRemainingToAssign}</>}</h1>
              <br/>
              {currencyLocal == ""?
              <div style={{marginBottom:"-185px"}}>
              <h2>
                You're not connected to Avalanche or Binance Smart Chain 
              </h2>
          <a href="https://docs.avax.network/build/tutorials/smart-contracts/deploy-a-smart-contract-on-avalanche-using-remix-and-metamask" target="_blank">Guide: How to change Metamask Network to Avalanche C-Chain Mainnet.</a><br/>
          <a href="https://academy.binance.com/en/articles/connecting-metamask-to-binance-smart-chain" target="_blank">Guide: How to change Metamask Network to Binance Smart Chain.</a><br/>
          <a href="https://docs.avax.network/build/tutorials/platform/transfer-avax-between-x-chain-and-c-chain" target="_blank">Need help transferring $AVAX to Metamask?</a><br/>
          <a href="https://medium.com/@leeds.harry/how-to-withdraw-bnb-on-binance-us-to-metamask-for-bakeryswap-7916807772f7" target="_blank">Need help transferring $BNB to Metamask?</a>
              </div>
              :
              null
              }
              
              {(this.state.runesRemainingToAssign <511 )&& (this.state.runesRemainingToAssign >=0)?
              
              <div>
                <h2>Rune recently claimed: </h2><br/>
                <Link to={"/rune/"+tempId} > <RuneImageComponent isDetail={true} runeId={tempId} divWidth={"225px"} height={"250"} width={"auto"} /> </Link>

              </div>
              
              :  
              <>
                {this.state.runesRemainingToAssign == 511?
                
              null
                  :

<div className="sk-folding-cube">
              <div className="sk-cube1 sk-cube"></div>
              <div className="sk-cube2 sk-cube"></div>
              <div className="sk-cube4 sk-cube"></div>
              <div className="sk-cube3 sk-cube"></div>
            </div>              }
              </>
              
              }
              


              {this.getClaimRunesComponent()}
          
              <br /> <br /> <br />
              Go to <Link to="/myRunes" >my runes ({this.state.myRunes.length}) </Link>page

              <br />

              Go to <Link to="/claimed" >claimed runes ({511 - this.state.runesRemainingToAssign}) </Link>page
              <br />

                Go to <Link to="/details" >details</Link> page
              </div>
              </div>
          </section>
        </div>
      );
    } else {
      return (
        <div className="App" >
          <section >
          <div className="sk-folding-cube">
  <div className="sk-cube1 sk-cube"></div>
  <div className="sk-cube2 sk-cube"></div>
  <div className="sk-cube4 sk-cube"></div>
  <div className="sk-cube3 sk-cube"></div>
</div>
          </section>
          <h2>
                Crypto Runes for Avalanche and Binance Smart Chain 
              </h2>
          <h3>You need Metamask to be able to use this site.</h3>
          <a href="https://docs.avax.network/build/tutorials/smart-contracts/deploy-a-smart-contract-on-avalanche-using-remix-and-metamask" target="_blank">Guide: How to change Metamask Network to Avalanche C-Chain Mainnet.</a><br/>
          <a href="https://academy.binance.com/en/articles/connecting-metamask-to-binance-smart-chain" target="_blank">Guide: How to change Metamask Network to Binance Smart Chain.</a><br/>
          <a href="https://docs.avax.network/build/tutorials/platform/transfer-avax-between-x-chain-and-c-chain" target="_blank">Need help transferring $AVAX to Metamask?</a><br/>
          <a href="https://medium.com/@leeds.harry/how-to-withdraw-bnb-on-binance-us-to-metamask-for-bakeryswap-7916807772f7" target="_blank">Need help transferring $BNB to Metamask?</a>

        </div>
      );
    }

  }
}

export default MarketPlaceHome;
