import React from 'react';
import './App.css';
import { Link} from "react-router-dom";

import { getWeb3, getInstance}  from "./Web3Util";
import getCurrencyTokenName from "./components/currencyUtils"

import RuneImageComponent from "./components/RuneImageComponent"


class RuneDetail extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      user: '',
      contractInstance: '',
      user:'',
      networkId:"",
      runesRemainingToAssign:0,
      myRuneCounter:0,
      myRunes:[],
      runeOwner:"",
      runePrice:0,
      runeForSale:null,
      bids:"",
      runeRecipientAddress:""
    };
    this.LoadData = this.LoadData.bind(this);
    this.getClaimPrice= this.getClaimPrice.bind(this);
    this.getTransferRuneComponenet = this.getTransferRuneComponenet.bind(this);
    this.changeRuneRecipientValue = this.changeRuneRecipientValue.bind(this);
    this.sendToAddress = this.sendToAddress.bind(this);
  }

  componentDidUpdate = async () => {
    const web3 = await getWeb3();
 
    
    await this.LoadData(web3);

  }

  componentDidMount = async () => {
    const web3 = await getWeb3();
    window.web3 = web3;
    const contractInstance = await getInstance(web3);
    window.user = (await web3.eth.getAccounts())[0];
    const networkId = await web3.eth.net.getId();
    this.setState({ user: window.user,hasData:true,contractInstance,networkId,runeOwner:""});
    
    await this.LoadData(web3);

  }
  async LoadData() {
      try {

       const runesRemainingToAssign = await this.state.contractInstance.methods.runesRemainingToAssign().call();
       const myRuneCounter = await this.state.contractInstance.methods.balanceOf(this.state.user).call();
       const myRunes = await this.state.contractInstance.methods.getAllRunesAtAddress(this.state.user).call();
       const runeOwner = await this.state.contractInstance.methods.runeIndexToAddress(this.props.match.params.runeId).call();
       const runeForSale =   await this.state.contractInstance.methods.runesOfferedForSale(this.props.match.params.runeId).call();
      const bids = await this.state.contractInstance.methods.runeBids(this.props.match.params.runeId).call();
       /**
         * runeForSale
         * 
        {0: true, 
        1: "108", 
        2: "0xF21EA9B94Bcc918b338116cCB66439b09BF69447",
         3: "1100000000000000000", 
         4: "0x0000000000000000000000000000000000000000",
          isForSale: true, 
          runeIndex: "108",
           seller: "0xF21EA9B94Bcc918b338116cCB66439b09BF69447", 
           minValue: "1100000000000000000", 
           onlySellTo: "0x0000000000000000000000000000000000000000"}

         */
        this.setState({
          runesRemainingToAssign,
          myRuneCounter,
          myRunes,
          runeOwner,
          runeForSale,
          bids
        })
    } catch (e) {console.log('Error', e)}
  
  }
  componentWillUnmount(){
    window.scrollTo(0, 0);
  }

  getClaimPrice(runeId){
    let prices = [0,0.75,1.5,3,6,12]
    let priceString = ""
    if(runeId>= 0 && runeId<100){
      priceString = "Rune claim price: "+prices[0]+" "+getCurrencyTokenName(this.state.networkId)
    }
    else if(runeId>= 100 && runeId<200){
      priceString = "Rune claim price: "+prices[1]+" "+getCurrencyTokenName(this.state.networkId)
    }
    else if(runeId>= 200 && runeId<300){
      priceString = "Rune claim price: "+prices[2]+" "+getCurrencyTokenName(this.state.networkId)
    }
    else if(runeId>= 300 && runeId<400){
      priceString = "Rune claim price: "+prices[3]+" "+getCurrencyTokenName(this.state.networkId)
    }
    else if(runeId>= 400 && runeId<500){
      priceString = "Rune claim price: "+prices[4]+" "+getCurrencyTokenName(this.state.networkId)
    }
    else if(runeId>= 500 && runeId<=511){
      priceString = "Rune claim price: "+prices[5]+" "+getCurrencyTokenName(this.state.networkId)
    }

    return priceString
  }

  getRuneNextComponents(runeId){
    let component =[]
    let tempId = Number.parseInt(runeId)

    if(runeId<=511 && runeId>0){
      let tempPrevious = tempId-1
      component.push(
        <Link to={"/rune/"+tempPrevious}>
          Previous rune
        </Link>
      )
      component.push(<>&nbsp;&nbsp;&nbsp;</>)
    }

    if(runeId>=0 && runeId<511){
      let tempNext= tempId+1
      component.push(
        <Link to={"/rune/"+tempNext}>
          Next rune
        </Link>
      )
    }
    return (

      <div>
        {component}
      </div>
    ) 
   }


  changeRuneRecipientValue(event){
    
    this.setState({
      runeRecipientAddress:event.target.value
    })
  }

  async sendToAddress(){
    try {
      const runeForSale = await this.state.contractInstance.methods.transferRune(this.state.runeRecipientAddress,this.props.match.params.runeId).send();
      this.setState({
        runeForSale
      })
  } catch (e) {console.log('Error', e)}
  }

  getTransferRuneComponenet(){
    return(
      <div>
      
      <br />
      <input placeholder={"Enter the reciever address"} style={{width:"450px"}} onChange={this.changeRuneRecipientValue}/>
      <br />
      {this.state.runeRecipientAddress.length >0 ?

      <button className="button-confirm" onClick={this.sendToAddress}>Confirm</button>
      : null
      }
        <br />
      <br />
      <br/>

      </div>

    );
  }

  render() {
    if (this.state.hasData) {
      let tempId =  this.props.match.params.runeId -510
      let localRuneId = Number.parseInt(this.props.match.params.runeId)
      if(tempId<0){
        tempId = tempId*(-1)
      }
      return (
        <div className="App"   >
          <section >
            <div >
              <div>
              <h1>
                Rune ID: {localRuneId}
              </h1>

{/** 
              {this.state.runesRemainingToAssign <= tempId?
              <>
                Rune owner: <span className="user-address">{this.state.runeOwner}</span>
              </>
              :
              <div>
              
              </div>
              }*/}
              {this.props.match.params.runeId<=511?
                 
                 <>
                <RuneImageComponent isDetail={true} runeId={this.props.match.params.runeId} divWidth={"225px"} height={"250"} width={"auto"} />
               
                {/*this.getClaimPrice(this.props.match.params.runeId)*/}

             
                {this.state.runesRemainingToAssign <= tempId?
                              
                              null:
                              <div>
                              Rune not yet claimed
                              </div>
                              }
                 </>
               :<>Nothing here</>}
               {this.getRuneNextComponents(this.props.match.params.runeId)} 

             <br/>
             
{this.state.runeOwner ==this.state.user?
this.getTransferRuneComponenet():
null }
             <br/><br/><br/>
              <Link to="/">go back to the homepage</Link>
              </div>
              </div>
          </section>
        </div>
      );
    } else {
      return (
        <div className="App" >
          <section >
          <div className="sk-folding-cube">
  <div className="sk-cube1 sk-cube"></div>
  <div className="sk-cube2 sk-cube"></div>
  <div className="sk-cube4 sk-cube"></div>
  <div className="sk-cube3 sk-cube"></div>
</div>          </section>
        </div>
      );
    }

  }
}

export default RuneDetail;
