import React from 'react';
import './App.css';
import getCurrencyTokenName from "./components/currencyUtils"
import { Link} from "react-router-dom";

import { getWeb3, getInstance}  from "./Web3Util";


class Details extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      user: '',
      contractInstance: '',
      user:'',
      networkId:"",
      runesRemainingToAssign:0,
      myRuneCounter:0,
      myRunes:[]
    };
    this.LoadData = this.LoadData.bind(this);

  }


  componentDidMount = async () => {
    const web3 = await getWeb3();
    window.web3 = web3;
    const contractInstance = await getInstance(web3);
    window.user = (await web3.eth.getAccounts())[0];
    const networkId = await web3.eth.net.getId();
    this.setState({ user: window.user,hasData:true,contractInstance,networkId });
    
    await this.LoadData(web3);
  }
  componentWillUnmount(){
    window.scrollTo(0, 0);
  }
  async LoadData() {
      try {

       const runesRemainingToAssign = await this.state.contractInstance.methods.runesRemainingToAssign().call();
       const myRuneCounter = await this.state.contractInstance.methods.balanceOf(this.state.user).call();
       const myRunes = await this.state.contractInstance.methods.getAllRunesAtAddress(this.state.user).call();

        this.setState({
          runesRemainingToAssign,
          myRuneCounter,
          myRunes
        })
    } catch (e) {console.log('Error', e)}
  
  }

  render() {
    let currency = getCurrencyTokenName(this.state.networkId)

    if (this.state.hasData) {
      return (
        <div className="App"   >
          <section >
            <div >
              <div>
              <h1>
                About Crypto Runes
              </h1>
           
              <br />
              Claim your Rune NFT on Avalanche and Binance Smart Chain.
              <br/><br/><br/>
              <h2>There are 6 tiers of runes:</h2>
              <br/><br/>
              {/** <br /><br/><br/>
              <h2>Rune prices:</h2><br/><br/>*/}
              Tier 1 - the first one hundred NFTs: are free (only pays gas fees)<br/><br/>
              Tier 2 - the following one hundred NFTs: 0,75 {currency}<br/><br/>
              Tier 3 - the following one hundred NFTs: 1,5 {currency}<br/><br/>
              Tier 4 - the following one hundred NFTs: 3 {currency}<br/><br/>
              Tier 5 - the following one hundred NFTs: 6 {currency}<br/><br/>
              Tier 6 - the last eleven NFTs: 12 {currency} <br/><br/>
              <h2>New runes and features:</h2><br/><br/>
              - Transfer rune feature added ✔️ <br/><br/>
              - New runes (work in progress 🚧)<br/><br/>
              - New features for rune holders  (work in progress 🚧)<br/><br/>
              <br/><br/><br/>
              Go back to the <Link to="/">homepage</Link>
              </div>
              </div>
          </section>
        </div>
      );
    } else {
      return (
        <div className="App" >
          <section >
          <div className="sk-folding-cube">
  <div className="sk-cube1 sk-cube"></div>
  <div className="sk-cube2 sk-cube"></div>
  <div className="sk-cube4 sk-cube"></div>
  <div className="sk-cube3 sk-cube"></div>
</div>
          </section>
        </div>
      );
    }

  }
}

export default Details;
